@import url("https://fonts.googleapis.com/css2?family=Muli&display=swap");

@media only screen and (max-width: 992px) {
  #header .animated{
    width: 90%!important;
  }
} 
@media only screen and (max-width: 766px) {
  #header .animated{
    width: 50%!important;
  }
}
@media only screen and (max-width: 600px) {
  #header .animated{
    width: 75%!important;
  }
} 
@media only screen and (min-width: 768px) {
  footer form .d-flex{
    margin-right: 80px;
  }
} 

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Muli", sans-serif;
}


.navbar-brand{
  font-size: 1.9rem;
  color: white!important;
}
.nav-link{
  color: white!important;
}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.navbar a:hover{
  color: #3498db!important;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

/* Button css */
.btn-outline-primary{
  border-radius:20px;
  transition: 0.5s;
  letter-spacing: 1px;
  color: white;
}

.makingBold h1{
  font-weight: bold;
  color: white;
}
.header-img{
  text-align: center;
}
.brand-name{
  color: #3498db;
}
.des-color{
  color: #F0FFFF;
}
.overlay-bg{
  top:0;
  left:0;
  width: 100%;
  height: 100vh;
  z-index:2;
  background-color: rgba(0, 0, 0, 0.6);
}
#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
  width: 60%;
}

.home-page{
    background: url('https://images.unsplash.com/photo-1497215842964-222b430dc094?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=1050&q=80');
    background-position:center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}
.about-page{
  background: url('https://images.unsplash.com/photo-1497215728101-856f4ea42174?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=1050&q=80');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}


@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
};



